.header-out {
  padding-top: 30px;
  @media (max-width: 460px) {
    margin-bottom: 0;
  }
}

.header {
  @extend .grid;
  align-items: center;
  position: relative;

  @media (max-width: 460px) {
    margin-bottom: 0;
    height: 76px;
  }

  &__logo {
    grid-column: 1 / 5;

    @media (max-width: 1024px) {
      grid-column: 1 / 3;
    }
  }

  &__wrapper {
    grid-column: 10 / 13;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 1024px) {
      grid-column: 6 / 9;
    }

    @media (max-width: 768px) {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      bottom: -50px;
      right: 50px;
      gap: 20px;
      transform: rotateX(90deg);
      transform-origin: top;
      z-index: 10;
    }

    &-visible {
      visibility: visible;
      opacity: 1;
      transform: rotateX(0);
    }
  }

  &__specs {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: $gray-50;
    text-transform: uppercase;

    &:hover {
      transform: scale(1.05);
    }
  }
}

.logo {
  display: flex;
  width: min-content;

  &:hover {
    transform: scale(1.05);
  }
}

.burger-active {
  & * {
    &:nth-of-type(1),
    &:nth-of-type(3) {
      top: 50%;
      left: 0;
      transform: rotate(-45deg);
      width: 50px;
    }

    &:nth-of-type(2) {
      display: none;
    }

    &:nth-of-type(3) {
      transform: rotate(45deg);
    }
  }
}
